import Vue from 'vue';
import Component from 'vue-class-component';

import { Action, State } from 'vuex-class';

// import { ITask } from '~/entity/task/index';
import Utils from '~/utils';
// import { CACHE_NAME } from '~/cache';
// import { IPayParams } from '~/entity/route_params';
import Api from '~/api';
import textField from 'muse-ui/textField';
import { ToastMsg } from '~/entity/toast';
import { IRegisterParams } from '~/entity/route_params';
import { CACHE_NAME } from '~/cache';

@Component({
    components: {
        'mu-text-field': textField
    }
})
export default class BindPhoneComp extends Vue {

    buttonTxt: string = '获取验证码';

    buttonEnable = false;

    submitEnable = false;

    phone: string = '';

    code: string = '';

    @Action('showToast')
    showToast;

    @Action('initUser')
    initUser;

    /**
     * 输入手机号， mu-text-field组件对oninput事件做了处理，直接返回了event.target.value。
     * @param value 
     */
    inputPhone(value) {
        // 验证手机号
        // console.log(value);
        this.phone = value;
        this.buttonEnable = Utils.checkPhoneNumber(value);
    }

    // 获取验证码
    async getCode() {
        this.buttonEnable = false;
        this.buttonTxt = '正在获取';

        try {
            const res = await Api.bindPhoneCode(this.phone);

            this.buttonTxt = '获取成功';
            this.submitEnable = true;
            let second = 60;

            const timer = setInterval(() => {
                if (second === 0) {
                    clearInterval(timer);
                    this.buttonTxt = '重新获取';
                    this.buttonEnable = true;
                    return;
                }
                this.buttonTxt = second + 's';
                second--;
            }, 1000);
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
            this.buttonTxt = '重新获取';
            this.buttonEnable = true;
        }
    }

    async bindPhone() {
        const code = this.code;
        const tel = this.phone;

        try {
            if (tel.length === 0) {
                throw new Error('手机号不能为空');
            }
    
            if (code.length === 0) {
                throw new Error('验证码不能为空');
            }

            const res = await Api.bindPhone(tel, code);
            const subStatus = res.body.subStatus;
            if (subStatus === 1100) {
                // 已注册用户 sex 1 男 2 女 0未知
                console.log('已注册用户');
                const url = Utils.getItem(CACHE_NAME.callbackUrl, false) || '/tab/ai';
                console.log('回调地址： ' + url);
                
                // 获取个人信息
                await this.initUser();

                this.showToast(new ToastMsg('绑定成功'));

                this['$router'].replace({
                    path: url
                });
                
            } else if (subStatus === 1101) {
                // 未注册
                console.log('未注册');
                const sex = Utils.getItem(CACHE_NAME.sex);

                const params: IRegisterParams = {
                    phone: tel,
                    sex
                };
                this['$router'].replace({
                    name: 'register',
                    params
                });       
            }
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }
}



